@import '../../../variables';

.ProductToBuy {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 8px;

  &:not(:last-child)::before {
    @include bottomBorder; }

  &-content {
    display: flex;
    align-items: center;
    width: 100%;
    &-img {
      height: 120px;
      img {
        height: inherit;
        display: block; } }

    &-data {
      width: 100%;
      margin-left: 12px;
      text-transform: capitalize;
      &-title {
        font-weight: 600 !important; } } }

  &-controls {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    align-self: stretch;
    font-weight: 500; } }

@media (max-width: 599.5px) {
  .ProductToBuy {
    padding: 10px 20px;
    &-content {
      &-img {
        height: 100px; } } } }

.MuiPaper-root.MuiMenu-paper.MuiPaper-elevation8.MuiPopover-paper.MuiPaper-rounded {
  &::-webkit-scrollbar {
    width: 0px !important; } }
