@import '../../variables';

.Cart {
  padding-top: 60px;
  &-miniInfo {
    margin-bottom: 50px !important;
    font-weight: 600 !important; }

  &-main {
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;

    &-products {
      width: 100%;
      max-width: 700px; }

    &-toCheckout {
      display: flex;
      flex-direction: column;
      align-self: flex-start;
      width: 100%;
      max-width: 280px;
      margin-left: 60px;
      padding: 20px;
      box-sizing: border-box;
      background: $primary-black;
      color: $white;
      &-main {
        margin-bottom: 20px;
        > div:last-child {
          font-weight: bold; } } } } }

@media (max-width: 959.5px) {
  .Cart {
    &-main {
      flex-direction: column;
      align-items: center;
      &-products {
        padding: 0 20px; }

      &-toCheckout {
        position: fixed;
        z-index: 100;
        left: 0;
        bottom: 0;
        max-width: 100%;
        margin: 0;
        padding: 0;
        background: $white;
        &-main {
          display: none; } } } } }

@media (max-width: 600px) {
  .Cart {
    &-miniInfo {
      margin-bottom: 40px; }
    &-main {
      padding: 0;
      &-products {
        padding: 0; } } } }
